import axios from 'axios'
import router from '../router.js'
import qs from 'qs'
import { Loading, Message } from 'element-ui'
// axios
// debugger
export const Axios = axios.create({
  baseURL: '/sq/',
  timeout: 50000,
  withCredentials: true // cookie
})

// request
Axios.interceptors.request.use(
  config => {
    return config
  },
  error => {
    Message.error({
      message: ''
    })
    return Promise.reject(error)
  }
)

// response
Axios.interceptors.response.use(
  // 
  res => {
    let data = res.data
    return data
  },
  // 
  error => {
    let res = error.response

    if (res) {
      switch (res.status) {
        //401  
        case 401:
          Message.error({
            message: ''
          })
          router.replace({
            path: '/login?flag=true'
          })
          break
      }
    } else {
      Message.error({
        message: ''
      })
    }
    return Promise.reject(error)
  }
)

// get
export function fetchGet(url, params) {
  return new Promise((resolve, reject) => {
    // get
    params = params ? params : ''

    Axios.get(url, {
      params: params
    })
      .then(
        res => {
          resolve(res)
        },
        error => {
          reject(error)
        }
      )
      .catch(error => {
        reject(error)
      })
  })
}

// post
export function fetchPost(url, params, contentType) {
  return new Promise((resolve, reject) => {
    // let _csrf = store.getters['_csrf'];
    let contentTypeUse =
      contentType == 'json'
        ? 'application/json;charset=UTF-8'
        : 'application/x-www-form-urlencoded;charset=UTF-8'
    let paramsUse = contentType == 'json' ? params : qs.stringify(params)

    // post
    // params = !params ? '' : qs.stringify(params);
    Axios.post(url, paramsUse, {
      headers: {
        'Content-Type': contentTypeUse
      }
    })
      .then(
        res => {
          resolve(res)
        },
        error => {
          reject(error)
        }
      )
      .catch(error => {
        reject(error)
      })
  })
}

// put
export function fetchPut(url, params, contentType) {
  return new Promise((resolve, reject) => {
    let contentTypeUse =
      contentType == 'json'
        ? 'application/json;charset=UTF-8'
        : 'application/x-www-form-urlencoded;charset=UTF-8'
    let paramsUse = contentType == 'json' ? params : qs.stringify(params)

    Axios.put(url, paramsUse, {
      headers: {
        'Content-Type': contentTypeUse
      }
    })
      .then(
        res => {
          resolve(res)
        },

        error => {
          reject(error)
        }
      )
      .catch(error => {
        reject(error)
      })
  })
}

// delete
export function fetchDelete(url, params) {
  return new Promise((resolve, reject) => {
    // get
    params = params ? params : ''
    Axios.delete(url, {
      params: params
    })
      .then(
        res => {
          resolve(res)
        },
        error => {
          reject(error)
        }
      )
      .catch(error => {
        reject(error)
      })
  })
}
