<template>
  <div>
    <div class="containerfgj">
      <!--  -->
      <div class="fujian"><span> /</span></div>
      <div class="conMain">
      <!-- <div class="tableHeading"></div> -->
      <div class="heading"></div>
      <!--  -->
      <div class="block">
        <div class="top-left">
        <div class="demonstration">:</div>
        <div class="selectedmonth">
          <!-- <el-input v-model="ModelTypeInput" @change="searchclick1()" placeholder=""></el-input> -->

          <el-select
            v-model="ModelTypeInput"
            @change="searchclick1()"
            filterable
            placeholder=""
          >
            <el-option
              v-for="item in typeNameOptions"
              :key="item.sensorTypeID"
              :label="item.sensorTypeName"
              :value="item.sensorTypeName"
            ></el-option>
          </el-select>
        </div>
        <span class="demonstration">:</span>
        <div class="selectedmonth">
          <!-- <el-input v-model="OrgNameInput" @change="searchclick2()" placeholder=""></el-input> -->

          <el-select
            v-model="OrgNameInput"
            @change="searchclick2()"
            filterable
            placeholder=""
          >
            <el-option
              v-for="item in orgNameOptions"
              :key="item.stationID"
              :label="item.stationName"
              :value="item.stationName"
            ></el-option>
          </el-select>
        </div>
        <span class="demonstration">:</span>
        <div class="selectedmonth">
          <el-select v-model="statusValue" @change="searchclick3()" placeholder="">
            <el-option
              v-for="item in statusOptionsAll"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
        </div>
         <div class="top-right">
        <el-button class="addmess" type="primary" @click="addclick()"></el-button>
      </div>
      </div>

      <!--  -->
      <div class="sectionTable">
        <el-table
          :data="rows"
          border
          stripe
          empty-text=""
          style="border-radius:6px;"
        >
          <el-table-column type="index"  label="" width="60"></el-table-column>
          <el-table-column prop="propertyCode"  label=""></el-table-column>
          <el-table-column prop="sensorTypeName"  label=""></el-table-column>
          <el-table-column prop="sensorCode"  label=""></el-table-column>
          <el-table-column prop="sensorModelType"  label=""></el-table-column>
          <el-table-column prop="sensorUseStatus"  label="">
            <template slot-scope="scope">
              <div v-if="scope.row.sensorUseStatus == 0 "></div>
              <div v-if="scope.row.sensorUseStatus == 1 "></div>
              <div v-if="scope.row.sensorUseStatus == 2 "></div>
              <div v-if="scope.row.sensorUseStatus == 3 "></div>
            </template>
          </el-table-column>
          <el-table-column
            prop="subordinateCompany"
            label=""
            width="140"
          ></el-table-column>
          <el-table-column prop="manageOrgName"  label=""></el-table-column>
          <el-table-column prop="createByName"  label=""></el-table-column>
          <el-table-column prop="createTime"  label="" width="95">
            <template slot-scope="scope">
              <div>{{scope.row.createTime | formatDate}}</div>
            </template>
          </el-table-column>
          <el-table-column prop="updateByName"  label=""></el-table-column>
          <el-table-column prop="updateTime" label="" width="95">
            <template slot-scope="scope">
              <div>{{scope.row.updateTime | formatDate}}</div>
            </template>
          </el-table-column>

          <el-table-column prop="remarks"  label=""></el-table-column>

          <!--  -->
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <div class>
                <span class="editButton" @click="editclick(scope.row)"></span>
                <span
                  style="
                    height:40px;
                    font-size:16px;
                    font-family:SimHei;
                    font-weight:bold;
                    color:#C9C9C9;
                    line-height:40px;"
                >|</span>
                <span class="editButton1" @click="deleteclick(scope.row)"></span>
              </div>
            </template>
          </el-table-column>
        </el-table>

        <div class="showAll">
          &nbsp;&nbsp;
          <span>{{showNum}}</span>&nbsp;&nbsp;
        </div>
      </div>
    </div>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogAddFormVisible"
      width="40%"
      @close="dialogAddVisibleClosed"
    >
      <el-form :model="AddForm" label-width="80px" ref="addFormRef" :rules="rulesOne">
        <el-form-item label="" prop="propertyCode">
          <el-input v-model="AddForm.propertyCode"></el-input>
        </el-form-item>

        <el-form-item label="" prop="sensorTypeId">
          <el-select v-model="AddForm.sensorTypeId" filterable placeholder="">
            <el-option
              v-for="item in typeNameOptions"
              :key="item.sensorTypeID"
              :label="item.sensorTypeName"
              :value="item.sensorTypeID"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="sensorCode">
          <el-input v-model="AddForm.sensorCode"></el-input>
        </el-form-item>

        <el-form-item label="" prop="sensorModelType">
          <el-input v-model="AddForm.sensorModelType"></el-input>
        </el-form-item>

        <el-form-item label="" prop="sensorUseStatus">
          <el-select v-model="AddForm.sensorUseStatus" placeholder="">
            <el-option
              v-for="item in statusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="subordinateCompany">
          <el-input v-model="AddForm.subordinateCompany"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-select v-model="AddForm.manageOrgId" filterable placeholder="">
            <el-option
              v-for="item in orgNameOptions"
              :key="item.stationID"
              :label="item.stationName"
              :value="item.stationID"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="remarks">
          <el-input v-model="AddForm.remarks"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogAddFormVisible = false"> </el-button>
        <el-button type="primary" @click="AddFormVisible()"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogEditFormVisible"
      width="30%"
      @close="dialogEditVisibleClosed"
    >
      <el-form :model="editForm" label-width="80px" ref="editFormRef" :rules="rulesTwo">
        <el-form-item label="" prop="propertyCode">
          <el-input v-model="editForm.propertyCode" disabled></el-input>
        </el-form-item>

        <el-form-item label="" prop="sensorTypeId">
          <el-select v-model="editForm.sensorTypeId" filterable placeholder="">
            <el-option
              v-for="item in typeNameOptions"
              :key="item.sensorTypeID"
              :label="item.sensorTypeName"
              :value="item.sensorTypeID"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="sensorCode">
          <el-input v-model="editForm.sensorCode"></el-input>
        </el-form-item>

        <el-form-item label="" prop="sensorModelType">
          <el-input v-model="editForm.sensorModelType"></el-input>
        </el-form-item>

        <el-form-item label="" prop="sensorUseStatus">
          <el-select v-model="editForm.sensorUseStatus" placeholder="">
            <el-option
              v-for="item in statusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="subordinateCompany">
          <el-input v-model="editForm.subordinateCompany"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-select v-model="editForm.manageOrgId" filterable placeholder="">
            <el-option
              v-for="item in orgNameOptions"
              :key="item.stationID"
              :label="item.stationName"
              :value="item.stationID"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="remarks">
          <el-input v-model="editForm.remarks"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogEditFormVisible = false"> </el-button>
        <el-button type="primary" @click="EditFormVisible()"> </el-button>
      </div>
    </el-dialog>
  </div>
  </div>
</template>
<script>
import { formatDate } from "./data.js";

export default {
  data() {
    return {
      ModelTypeInput: "", //input
      OrgNameInput: "", //input
      statusValue: 0, //
      typeNameOptions: [],
      statusOptions: [
        {
          value: "0",
          label: ""
        },
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        }
      ],
      statusOptionsAll: [
        {
          value: 4,
          label: ""
        },
        {
          value: 0,
          label: ""
        },
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        },
        {
          value: 3,
          label: ""
        }
      ],
      orgNameOptions: [],
      rows: [], //
      showNum: 0,
      dialogAddFormVisible: false,
      dialogEditFormVisible: false,
      AddForm: {
        propertyCode: "",
        sensorTypeId: "",
        sensorCode: "",
        sensorModelType: "",
        sensorUseStatus: "",
        subordinateCompany: "",
        manageOrgId: "",
        remarks: ""
      },

      editForm: {
        propertyCode: "",
        sensorTypeId: "",
        sensorCode: "",
        sensorModelType: "",
        sensorUseStatus: "",
        subordinateCompany: "",
        manageOrgId: "",
        remarks: ""
      },

      //
      rulesOne: {
        propertyCode: [
          { required: true, message: "", trigger: "blur" },
          // { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' },
          {
            pattern: /^([0-9A-Za-z\.\@\!\#\$\%\^\-\&\*\(\)]){1,20}$/,
            message: "1-20(-!@#$%^&*())",
            trigger: "blur"
          }
        ],
        sensorTypeId: [
          { required: true, message: "", trigger: "change" }
        ],
        sensorCode: [
          { required: false, message: "", trigger: "blur" },
          {
            pattern: /^([0-9A-Za-z\.\@\!\#\$\%\^\-\&\*\(\)]){1,20}$/,
            message: "1-20(-!@#$%^&*())",
            trigger: "blur"
          }
        ],
        sensorModelType: [
          { required: true, message: "", trigger: "blur" },
          { max: 20, message: " 20 ", trigger: "blur" }
        ],
        sensorUseStatus: [
          { required: true, message: "", trigger: "change" }
        ],
        subordinateCompany: [
          { required: true, message: "", trigger: "blur" },
          //  {
          //   pattern: /^[0-9A-Za-z]{1,20}$/,
          //   message: " 1  20 ",
          //   trigger: "blur"
          // }
          { max: 20, message: " 20 ", trigger: "blur" }
        ],
        // manageOrgId: [
        //   { required: false, message: '', trigger: 'change' }
        // ],
        remarks: [
          // { required: false, message: '', trigger: 'blur' },
          { max: 100, message: " 100 ", trigger: "blur" }
        ]
      },

      //
      rulesTwo: {
        propertyCode: [
          { required: true, message: "", trigger: "blur" },
          // { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' },
          {
            pattern: /^([0-9A-Za-z\.\@\!\#\$\%\^\-\&\*\(\)]){1,20}$/,
            message: "1-20(-!@#$%^&*())",
            trigger: "blur"
          }
        ],
        sensorTypeId: [
          { required: true, message: "", trigger: "change" }
        ],
        sensorCode: [
          { required: false, message: "", trigger: "blur" },
          {
            pattern: /^([0-9A-Za-z\.\@\!\#\$\%\^\-\&\*\(\)]){1,20}$/,
            message: "1-20(-!@#$%^&*())",
            trigger: "blur"
          }
        ],
        sensorModelType: [
          { required: true, message: "", trigger: "blur" },
          { max: 20, message: " 20 ", trigger: "blur" }
        ],
        sensorUseStatus: [
          { required: true, message: "", trigger: "change" }
        ],
        subordinateCompany: [
          { required: true, message: "", trigger: "blur" },
          { max: 20, message: " 20 ", trigger: "blur" }
          //  {
          //   pattern: /^[0-9A-Za-z]{1,20}$/,
          //   message: " 1  20 ",
          //   trigger: "blur"
          // }
        ],
        // manageOrgId: [
        //   { required: false, message: '', trigger: 'change' }
        // ],
        remarks: [
          // { required: false, message: '', trigger: 'blur' },
          { max: 100, message: " 100 ", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    // this.getAllData();
    this.searchclick3();
    this.getselectList();
  },
  filters: {
    //
    formatDate(time) {
      // console.log('' ,time);
      if (time == undefined) {
        return;
      } else {
        let date = new Date(time);
        return formatDate(date, "yyyy-MM-dd hh:mm:ss");
      }
    }
  },
  mounted() {},
  methods: {
    //
    getAllData() {
      this.$fetchGet("sensorDatabase/getAllAndName")
        .then(res => {
          // console.log("11111", res);
          this.rows = res;
          this.showNum = res.length;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    getselectList() {
      //
      this.$fetchGet("sensorType/getAllIDAndName")
        .then(res => {
          // console.log("ttt", res.data);
          this.typeNameOptions = res;
        })
        .catch(function(error) {
          console.log(error);
        });
      //
      this.$fetchGet("RiverStation/getAllIDAndName")
        .then(res => {
          // console.log("rrr", res.data);
          this.orgNameOptions = res;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    searchclick1() {
      this.OrgNameInput = "";
      this.statusValue = "";
      this.$fetchGet("sensorDatabase/findAllByTypeName", {
        sensorTypeName: this.ModelTypeInput
      })
        .then(res => {
          // console.log("22222", res);
          this.rows = res;
          this.showNum = res.length;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    searchclick2() {
      this.ModelTypeInput = "";
      this.statusValue = "";
      this.$fetchGet("sensorDatabase/findAllByOrgName", {
        manageOrgName: this.OrgNameInput
      })
        .then(res => {
          // console.log("3333", res);
          this.rows = res;
          this.showNum = res.length;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    searchclick3() {
      this.ModelTypeInput = "";
      this.OrgNameInput = "";
      if (this.statusValue === 4) {
        this.getAllData();
      } else {
        this.$fetchGet("sensorDatabase/findAllByUseStatus", {
          sensorUseStatus: this.statusValue
        })
          .then(res => {
            // console.log("44444", res.length);
            this.rows = res;
            this.showNum = res.length;
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },

    addclick() {
      this.dialogAddFormVisible = true;
    },

    AddFormVisible() {
      this.$refs.addFormRef.validate(res => {
        if (res) {
          // console.log("res", res);
          // console.log("AddForm", this.AddForm);
          // 1.
          this.$fetchPost("sensorDatabase/insert", this.AddForm, "json").then(
            res => {
              // console.log("", res);
              // 2.
              this.dialogAddFormVisible = false;
              this.$refs.addFormRef.resetFields();
              // 3.
              if (res === 1) {
                this.$message({
                  message: "",
                  type: "success",
                  duration: 2000
                });
              } else if (res.data === -1) {
                this.$message({
                  message: "ID",
                  type: "error",
                  duration: 2000
                });
              } else {
                this.$message({
                  message: ",",
                  type: "error",
                  duration: 2000
                });
              }
              // 4.
              this.getAllData();
            }
          );
        } else {
          this.$message({
            message: ",",
            type: "error",
            duration: 1000
          });
          return false;
        }
      });
    },

    EditFormVisible() {
      this.$refs.editFormRef.validate(res => {
        if (res) {
          // console.log("res", res);
          // console.log("AddForm", this.AddForm);
          // 1.
          this.$fetchPost("sensorDatabase/update", this.editForm, "json").then(
            res => {
              // console.log("", res);
              // 2.
              this.dialogEditFormVisible = false;
              // this.$refs.editFormRef.resetFields();
              // 3.
              if (res === 1) {
                this.$message({
                  message: "",
                  type: "success",
                  duration: 2000
                });
              } else {
                this.$message({
                  message: ",",
                  type: "error",
                  duration: 2000
                });
              }
              // 4.
              this.getAllData();
            }
          );
        } else {
          this.$message({
            message: ",",
            type: "error",
            duration: 1000
          });
          return false;
        }
      });
    },

    //
    editclick(res) {
      // console.log('',res);
      const {
        propertyCode,
        sensorTypeId,
        sensorCode,
        sensorModelType,
        sensorUseStatus,
        subordinateCompany,
        manageOrgId,
        remarks
      } = res;
      this.editForm.propertyCode = propertyCode;
      this.editForm.sensorTypeId = sensorTypeId;
      this.editForm.sensorCode = sensorCode;
      this.editForm.sensorModelType = sensorModelType;
      this.editForm.sensorUseStatus = sensorUseStatus;
      this.editForm.subordinateCompany = subordinateCompany;
      this.editForm.manageOrgId = manageOrgId;
      this.editForm.remarks = remarks;

      this.dialogEditFormVisible = true;
    },

    // 
    dialogAddVisibleClosed() {
      this.$refs.addFormRef.clearValidate();
      this.$refs.addFormRef.resetFields();
    },

    //
    dialogEditVisibleClosed() {
      this.$refs.editFormRef.clearValidate();
      // this.$refs.editFormRef.resetFields();
    },

    deleteclick(res) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          let paramsid = res.propertyCode;
          this.$fetchGet("sensorDatabase/delete", {
            propertyCode: paramsid
          }).then(res => {
            // console.log("ssss", res);
            if (res == 1) {
              // 
              this.$message({
                message: "",
                type: "success",
                duration: 1000
              });

              this.getAllData();
            } else {
              this.$message({
                message: "",
                type: "error",
                duration: 1000
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  }
};
</script>
<style type="text/css">
.block .el-input__icon {
  height: 30px;
  line-height: 30px;
}

/* .sectionTable .el-table td,
.el-table th.is-leaf {
  color: #696969;
} */

.containerfgj .sectionTable .el-table td {
  /* .container123 .sectionTable .el-table th.is-leaf { */
  color: #606266;
  font-weight: normal;
}

/* 
.sectionTable .el-table {
  color: #606266;
} */
</style>

<style type="text/css" scoped>
.containerfgj {
  width: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
.conMain {
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 1);
  text-align: center;
  margin: 10px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.heading {
  width: 100%;
  height: 23px;
  text-align: center;
  font-size: 22px;
  font-weight: 400;
  color: rgba(33, 140, 246, 1);
  line-height: 30px;
  margin-top: 33px;
}
.tableHeading {
  width: 100%;
  height: 25px;
  font-size: 14px;
  font-weight: 400;
  color: rgba(152, 152, 152, 1);
  line-height: 35px;
  margin-top: 10px;
  text-align: center;
}
.block {
  width: 100%;
  height: 35px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 14px;
  margin-top: 18px;
  box-sizing: border-box;
  padding: 0 30px;
  padding-left: 0;
}
.top-left {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.fujian {
  font-size: 14px;
  font-weight: 400;
  color: #555555;
  line-height: 30px;
  width: 100%;
  height: 20px;
  margin-left: 10px;
}
.fujian span {
  font-size: 14px;
  font-weight: 400;
  color: rgba(152, 152, 152, 1);
  line-height: 30px;
  height: 20px;
}
.editButton {
  /* width: 40px; */
  height: 40px;
  font-size: 14px;
  font-family: SimHei;
  font-weight: bold;
  color: rgba(33, 140, 246, 1);
  line-height: 40px;
  cursor: pointer;
}
.editButton1 {
  height: 40px;
  font-size: 14px;
  font-family: SimHei;
  font-weight: bold;
  color: rgba(246, 89, 82, 1);
  line-height: 40px;
  cursor: pointer;
}
.demonstration {
  width: 80px;
  height: 35px;
  font-size: 14px;
  font-weight: 400;
  color: rgba(102, 102, 102, 1);
  line-height: 35px;
  margin-left: 10px;
}
.selectedmonth {
  width: 160px;
  height: 30px;
  margin-right: 25px;
}
.dc {
  border-radius: 8px;
  font-size: 16px;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  line-height: 3px;
  text-align: center;
}
.addmess {
  border-radius: 8px;
  font-size: 16px;
  font-family: SimHei;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  line-height: 3px;
  margin-left: 12px;
}

.showAll {
  font-size: 19px;
  margin-top: 20px;
  margin-left: 40px;
  color: #696969;
}
.showAll > span {
  color: black;
}
</style>